import {
  Add,
  PlayArrow,
  ThumbDownAltOutlined,
  ThumbUpAltOutlined,
} from "@material-ui/icons";
import React, { useState,useEffect,useContext} from "react";
import "./listItem.scss";
import ReactDOM from 'react-dom';
import ChartistGraph from 'react-chartist';
import axios from "axios";
import { Link } from "react-router-dom";
import { AuthContext } from "../../authContext/AuthContext";


function ListItem({ index , item}) {
 
  const {user} = useContext(AuthContext);
  const [isHovered, setIsHovered] = useState(false);
  const [movie , setMovie] = useState({});
//   const trailer =
//  "https://www.youtube.com/embed/LQVQHLEPZQM";
useEffect(()=>{
const getMovie = async ()=>{
  try{
    const res = await axios.get("https://abfkh-netflix-api.herokuapp.com/api/movies/find/"+item ,{
      headers:{
        token: `Bearer ${user.accessToken}`
    }
  });
  setMovie(res.data);
  console.log(res);

  }catch(err){
    console.log(err);
  }
};

getMovie()
},[item]);

//   const trailer =
//  "https://www.youtube.com/embed/LQVQHLEPZQM";

  return (
    <Link to={{pathname:"/watch", movie:movie}}>

    <div
    
      className="listItem"
      style={{ left: isHovered && index * 225 - 50 + index * 2.5 }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}

    >
      <img
        src={movie ? movie.img?movie.img :"":""}
        alt=""
        

      />
      
      {isHovered && (
       <>
          {/* <video src={movie? movie.trailer :""} allow="autoPlay; fullscreen;" frameBorder="0"  width="240" height="140"/> */}
          <video className="video" autoPlay={true} onProgress  src={movie? movie.trailer :""}/>

         

          <div className="itemInfo">
            <div className="icons">
              <PlayArrow className="icon" />
              <Add className="icon" />
              <ThumbUpAltOutlined className="icon" />
              <ThumbDownAltOutlined className="icon" />
            </div>
            <div className="itemInfoTop">
              <span>{movie? movie.duration :""}</span>
              <span className="limit">{movie? movie.limit :""}</span>
              <span>{movie? movie.year :""}</span>
            </div>

            <div className="desc">
              
                {movie ? movie.desc :""}
              
            </div>
            <div className="genre">{movie ? movie.genre :""}</div>
          </div>
       </>
     )} 
        
      
    </div>
    </Link>
  );
}

export default ListItem;

