import { Height, InfoOutlined, PlayArrow } from '@material-ui/icons'
import React ,{ useContext, useEffect, useState } from 'react'
import axios from "axios";
import { Link } from "react-router-dom";


import "./featured.scss"
import { AuthContext } from '../../authContext/AuthContext';
 function Featured({ type1 }) {
     
    const [content,setContent] = useState({});
    const {user,dispatch} = useContext(AuthContext)
    useEffect(()=>{
        const getRandomContent = async ()=> {
            try{
                const res = await axios.get(`https://abfkh-netflix-api.herokuapp.com/api/movies/random?type=${type1}`, {
                   headers:{
                       token: `Bearer ${user.accessToken}`
                   }});
                   console.log("ffreferferferferferferferfref",res)
                setContent(res.data[0]);

            }catch(err){
                console.log(err);
            }
        };
        getRandomContent();   
console.log("content: \n",content)
    },[type1]);
    

    return (
        
        <div className="featured">
            {type1 && (

                <div className="category">
                    <span>{type1 === "movies" ? "Movies" : "Series"}</span>
                    <select name="genre" id="genre" >
                        <option>Genre</option>
                        <option value="adventure">Adventure</option>
                        <option value="comedy">Comedy</option>
                        <option value="crime">Crime</option>
                        <option value="fantasy">Fantasy</option>
                        <option value="historical">Historical</option>
                        <option value="horror">Horror</option>
                        <option value="romance">Romance</option>
                        <option value="sci-fi">Sci-fi</option>
                        <option value="thriller">Thriller</option>
                        <option value="western">Western</option>
                        <option value="animation">Animation</option>
                        <option value="drama">Drama</option>
                        <option value="documentary">Documentary</option>
                       

                    </select>
                </div>
            )}
            
                        <img src={content.img} alt="" />
                        
                        <div className="info">
                        <img src={content.imgTitle} alt="" />
                    
                        <span className="desc">{content.desc}</span>
                        <div className="button">

                        <Link style={{textDecoration:"none"}} to={{pathname:"/watch", movie:content}}>
                            <button className="play">
                                <PlayArrow/>
                                <span >paly</span>
                            </button>
                            </Link>
                            <button className="more">
                                <InfoOutlined/>
                                <span>info</span>
                            </button>
                        </div>
                        
                
                        </div>
                        <div class="banner--fedeBottom"></div>
                        

            
        </div>
    )
}

export default Featured;
