import { ArrowDropDown, Notifications, NotificationsNone, Search } from '@material-ui/icons'
import React, { useContext, useState } from 'react'
import { Link } from 'react-router-dom';
import { logout } from '../../authContext/AuthActions';
import { AuthContext } from '../../authContext/AuthContext';
import "./navbar.scss"

const Navbar = () => {
    const [isScrolled, setIsScrolled] = useState(false);
    const {user,dispatch} = useContext(AuthContext)
    window.onscroll = () =>{
        setIsScrolled(window.pageYOffset === 0 ? false : true);
        return () =>(window.onscroll = null);

    };
    
    return (
        <div className={isScrolled ? "navbar scrolled" : "navbar"}>
            <div className="container">
            <div className="left">
                <img src="https://www.freepnglogos.com/uploads/netflix-logo-text-emblem-31.png" alt="" />
                <Link to="/" className="link">
                <span>Home page</span>
                </Link>
                <Link to="/series" className="link">
                <span className="navbarnainLinks" >Series</span>
                </Link>
                <Link to="/movies" className="link">
                <span className="navbarnainLinks" >Movies</span>
                </Link>
                
                <span>New and Pouplar</span>
                <span>My list</span>
            </div>
            <div className="right">
                <Search className="icon" />
                <span>You</span>
                <img src="https://ih0.redbubble.net/image.618427277.3222/flat,1000x1000,075,f.u2.jpg" alt="" />
                <Notifications className="icon"/>
              
              <div className="profile">
                   <ArrowDropDown className="icon"/>
                   <div className="options">
                       <span>Setting</span>
                       <span onClick={() => dispatch(logout())}>Logout</span>
                   </div>
              </div>
              
            </div>
           

            </div>
           
        </div>
    )
}

export default Navbar
