const AuthReducer = (state, action) => {
    console.log(action.type);
    switch (action.type) {
        case "LOGIN_START":
            console.log("im in start");
            return {
                user: null,
                isFetching: true,
                error: false,


            };
        case "LOGIN_SUCCESS":
            console.log("im in success");
            console.log('action: ', action);
            return {
                user: action.payload,
                isFetching: false,
                error: false,


            };
        case "LOGIN_FAILURE":
            console.log("im in failure");
            return {
                user: null,
                isFetching: false,
                error: true,


            };
        case "LOGOUT":

            return {
                user: null,
                isFetching: false,
                error: false,


            };
        default:
            console.log("im in default");
            return {...state };
    }
};
export default AuthReducer;