//app
import "./app.scss"
import Home from "./pages/home/Home";
import Register from "./pages/home/rigister/Register";
import Login from "./pages/home/login/Login";
import Watch from "./pages/home/watch/Watch";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
  
} from "react-router-dom";
import {AuthContext} from "./authContext/AuthContext";
import { useContext } from "react";
// import {
//   Switch
// } from "react-router";

const App = () => {
  const{user} = useContext(AuthContext);
  return (
  <Router>
  <Switch>
  <Route exact path="/">
  {user ? <Home type="series"/> : <Redirect to="register"/>}
  {/* {user ? <Home type="series"/> : <Redirect to="register"/>} */}
       </Route>
  <Route exact path="/register">
  {!user ? <Register/> : <Redirect to="/"/>}
       </Route>
  <Route exact path="/login">
  {!user ? <Login/> : <Redirect to="/"/>}
       </Route>

       
  {/* <Route exact path="/watch">
  {user ? <Watch/> : <Redirect to="/login"/>}
       </Route>
  <Route exact path="/series">
   {!user ? <Redirect  to="/series"/>:''}
       </Route>
  <Route exact path="/movies">
  {!user ? <Home/> : <Redirect to="/movies"/>}
       </Route> */}

       
       
       {user&&(
         <Route path="/movies">
         <Home type="movies" />
         </Route>
       )}
       {user&&(
          //  come here
         <Route path="/series">         
         <Home type="series" />
         </Route>
       )}
       {user&&(
         <Route path="/watch">
         <Watch/>
         </Route>
       )}
    
    {/* {user&(
      <>
      <Route path="/movies">
      <Home type="movies" />
      </Route>
   <Route path="/series">
      <Home type="series" />
      </Route>
   <Route path="/watch">
   <Watch/>
      </Route>
      </>
    )} */}
    
  </Switch>
  </Router>
  ) ;
};

export default App;
