import React, { useContext, useRef, useState } from 'react'
import { login } from '../../../authContext/apiCalls'
import { AuthContext } from '../../../authContext/AuthContext'
import "./login.scss"
function Login() {
    const [email,setEamil] = useState("") 
    const [password,setPassword] = useState("") 
    const{dispatch} = useContext(AuthContext)
    const handleLogin = (e) =>{
        e.preventDefault()
        login({email,password},dispatch);
    };
   
    
   

    return (
        <div className="login">
            <div className="top">
            <div className="wrapper">

                
                <img className="logo" src="https://www.freepnglogos.com/uploads/netflix-logo-text-emblem-31.png" alt="" />
                
            </div>
            </div>
            <div className="container">
                <form>
                    <h1>Sign In</h1>
                    <input type="email" placeholder="Email or phone number" onChange={(e)=>setEamil(e.target.value)}/>
                    <input type="password" placeholder="Password" onChange={(e)=>setPassword(e.target.value)}/>
                    <button className="loginButton"onClick={handleLogin}>Sign In</button>
                    <span>New to Netflix <b>sign up now.</b></span>
                    <small>This page is protected by Google reCAPTCHA to ensure you're not a bot <b>learn more</b></small>
                    
                </form>
                
               
            </div>
        </div>
    )
}

export default Login
