import React, { useRef, useState } from 'react'
import "./register.scss"
import axios from "axios";
import { useHistory } from 'react-router';
import { Link } from "react-router-dom";
import Login from '../login/Login';

function Register() {
    const [email,setEamil] = useState("") 
    const [password,setPassword] = useState("") 
    const [username,setUsername] = useState("") 
    const history = useHistory()
    const emailRef = useRef()
    const passwordRef = useRef()
    const usernameRef = useRef()
    const handleStart = () =>{
        setEamil(emailRef.current.value);
       
        

    }
    const handleFinish = async (e) =>{
        // console.log(passwordRef.current.value);
        // console.log(usernameRef.current.value);
       
        e.preventDefault();
        setPassword(passwordRef.current.value);
        setUsername(usernameRef.current.value);
        // const username = usernameRef.current.value;
        // const password = passwordRef.current.value;
        try{
            

            console.log("user",username,"email",email,"pass",password)
            await axios.post("https://abfkh-netflix-api.herokuapp.com/api/auth/register", {username,email,password});
            history.push("/login");

        }catch(err){
           

        }
       
    }

    return (
        <div className="register">
            <div className="top">
            <div className="wrapper">

                
                <img className="logo" src="https://www.freepnglogos.com/uploads/netflix-logo-text-emblem-31.png" alt="" />
                {/* <Link style={{textDecoration:"none"}} to={{pathname:"/login"}}>
                <button className="loginButtom" onClick={Login}>Sign in</button>
                </Link> */}

                <Link style={{textDecoration:"none", zIndex:2}} to={{pathname:"/login", Login}}>
                            <button  type="button" className="ccc" >
                                
                                <span >Sign In</span>
                            </button>
                            </Link>
            </div>
            </div>
            <div className="container">
                <h1>Unlimited movies, TV shows, and more</h1>
                <h2>Watch anywhere = Cancel anytime.</h2>
                <p>Ready to Watch ? Enter your email to create or restart yout membership</p>
                {!email ? (
                     <div className="input">
                     <input type="email" placeholder="emsil addres" ref={emailRef}/>
                     <button className="resgisterButton" onClick={handleStart}>Get started</button>
                 </div>
                ) : (
                    <form className="input">
                     <input type="username" placeholder="username" ref={usernameRef}/>
                     <input type="password" placeholder="password" ref={passwordRef}/>
                     <button className="resgisterButton" onClick={handleFinish}>Start</button>
                 </form>

                )}
               
            </div>
        </div>
    )
}

export default Register
