import { ArrowBack } from '@material-ui/icons'
import React from 'react'

import { Link, useLocation } from 'react-router-dom';
import "../watch/watch.scss"


function watch() {
    const location = useLocation();
        console.log(location);
      const movie = location.movie;
    return (
        <div className="watch">
            <Link to="/series">
            <div className="back">
                <ArrowBack/>
                Home
            </div>
            </Link>
            <video style={{color:"white"}} className="video" autoPlay={true} onProgress  controls src={movie.video} />
            
            {/* <div className="video">
            <iframe  width="560" height="315" src="https://www.youtube.com/embed/LQVQHLEPZQM?autoplay=1" title="YouTube video player" frameborder="0" allow="accelerometer; fullscreen; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            </div> */}
            
        </div>
    )
}

export default watch
